@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/material-Icons/MaterialIcons-Regular.eot');
  src: url('../fonts/material-Icons/MaterialIcons-Regular.woff2') format('woff2'), url('../fonts/material-Icons/MaterialIcons-Regular.woff') format('woff'), url('../fonts/material-Icons/MaterialIcons-Regular.ttf') format('truetype');
}
.iconic {
  text-rendering: auto;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 18px;
  /* Preferred icon size */
  width: 1em;
  height: 1em;
  line-height: inherit;
  text-transform: none;
  vertical-align: middle;
  transform: translate(0, -1px);
  speak: none;
